.crew-wrapper{font:100% "Roboto",sans-serif;display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center;transition:all 0.3s ease-in;transform:translateY(-10px)}.crew-wrapper .inactive{transition:all 0.3s ease-in;transform:translateY(40px)}.Bartender{width:70%;align-self:center}.Bartender img{width:100%;height:auto}.Bartender .bartender-info{display:flex;align-content:center;justify-content:center;flex-direction:column;font-size:1vw;margin-top:-10px;width:100%;margin-left:-4px}.Bartender .bartender-info span{text-transform:capitalize;align-self:center;margin-top:-10px;font-weight:bold}.readyNr{color:#f7b71e;margin-top:-15px}.MostSoldBeer div{display:grid;grid-template-columns:1fr;justify-self:center;align-self:center}.MostSoldBeer img.beer-tap-img{width:120px;height:150px;margin-top:0px;justify-content:space-around;border:5px solid transparent;border-radius:50%;box-shadow:0px 0px 0px 8px #f7b71e;box-sizing:border-box;transition:all 0.3s ease-in;margin-bottom:15px}#mostSoldP{margin-top:-35px}.tap-bars{display:grid;grid-template-columns:repeat(7, 1fr);align-items:end;position:relative;bottom:0.7vw;grid-gap:0.8vw;padding-inline:4vw;padding-left:4vw;padding-right:4vw}.each-tap{display:grid;align-items:end}.each-tap *{grid-area:1/1;margin:0 3px;border-radius:10px}.each-tap .capacity{background-color:#262d3b}.each-tap .level{background-color:#f7b71e;border-radius:1px 1px 6px 6px;margin-inline:0.5vw;margin-block:0.2vw;margin-left:0.5vw;margin-right:0.5vw;margin-bottom:0.2vw}.each-tap p{-webkit-writing-mode:vertical-lr;-ms-writing-mode:tb-lr;writing-mode:vertical-lr;place-self:end center;transform:rotate(180deg);padding-inline:0.5rem;padding-top:0.5rem}.Clock{display:flex;justify-content:center;justify-items:center;flex-direction:column}.Clock h1{margin-top:0px}.Clock p{margin-top:-20px}.Clock .clock{display:flex;justify-content:center;align-items:center}.Header{width:100%;color:#fff;display:grid;grid-template-columns:repeat(4, 1fr);color:#eeb61c;justify-content:space-between;margin-top:10px;font:100% "Amatic SC",cursive}.Header .logo-wrapper{padding:20px;display:flex;justify-content:center;margin-left:50px;align-items:flex-start;align-content:baseline}.Header .logo-wrapper h1{margin:0;padding:0;font-size:4em;margin-left:5px;align-self:end}.Header .logo-wrapper .logo{width:70px;margin:0;padding:0}.Header .Clock{grid-column:4/4}.togglebtn{color:#24262b;background-color:#fff;border-radius:57px;padding:0px 20px;border:none;text-decoration:none;text-transform:capitalize;font-weight:500;font-size:1rem;transition:all 0.3s ease-in-out;cursor:pointer;margin-right:40px;height:50px;align-self:center;font-weight:600;grid-column:3/4;margin-bottom:70px}.App{text-align:center;display:grid;grid-template-rows:auto 1fr}.Main{display:grid;grid-template-columns:1.8fr 1fr 1fr 1fr 1.8fr;grid-template-rows:repeat 2,40vh;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px;grid-row-gap:25px;row-gap:25px;background-color:#232121;height:100%;padding:0px 20px;margin-bottom:15px}.MostSoldBeer{grid-column-start:1;grid-column-end:2;grid-row-start:1;grid-row-end:2;height:100%;width:100%;border-radius:20px}.Serving{grid-column-start:2;grid-column-end:5;grid-row-start:1;grid-row-end:2;height:100%;width:100%;border-radius:20px}.Crew{grid-column-start:3;grid-column-end:5;grid-row-start:2;grid-row-end:3;height:100%;width:100%;border-radius:20px}.Taps{grid-column-start:1;grid-column-end:3;grid-row-start:2;grid-row-end:3;height:100%;width:100%;border-radius:20px}.Queue{grid-column-start:5;grid-column-end:-1;grid-row-start:1;grid-row-end:3;height:100%;width:100%;border-radius:20px;overflow-y:scroll}.panel{border-radius:77px;font:100% "Roboto",sans-serif}.panel h1{font-weight:300}.Order{display:grid;grid-template-columns:1fr 1fr 3fr;font:100% "Amatic SC",cursive;font-size:2em;width:100%;flex-wrap:nowrap;align-items:baseline}.line-number{font-weight:bold;font-size:2rem;margin:0}.order .order-id{color:#eeb61c}.order *{text-align:left;color:#eeb61c;margin:0;align-self:center}.highlight{color:#eeb61c}.beerimg{width:auto;height:40px}.order-details{text-align:left;margin-left:10px}.serving{margin-left:10px;display:flex;color:black;flex-wrap:nowrap}.QueueList{height:100%;padding-top:20px}

